.PROGRAMER span {
  font-size: 2rem;
  font-weight: bold;
  color: white;
  position: relative;
  font-family: Arial;
  text-shadow:
  0 0 5px rgba(0,178,255,1),
  0 0 10px rgba(0,178,255,1),
  0 0 20px rgba(0,178,255,1),
  0 0 40px rgba(38,104,127,1),
  0 0 80px rgba(38,104,127,1);
  cursor: pointer;
  transition: all 300ms ease-in-out;
}